.skillsBlock {
    min-height: 50vh;
    background-color: hotpink;
    display: flex;
    justify-content: center;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
    flex-wrap: nowrap;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.title {
    /*width: 400px;*/
    background-color: grey;
}

.icon {
    background-color: hotpink;
    width: 100px;
    height: 100px;
}